:root {
  --background-color: #383838;
  --text-color: #fff;
  --color-white: #fff;
  --color-graphite-dark: #2f2f2f;
  --color-graphite: #383838;
  --color-text-secondary: #555;
  --color-bg-light: #f9f9f9;
  --color-bg-hover: #f1f1f1;
  --color-border: silver;
  --color-shadow: #0000000d;
  --color-accent: yellow;
  --container-max-width: 1200px;
}

html {
  scrollbar-gutter: stable;
}

header {
  border-bottom: var(--color-accent) solid 1px;
  box-sizing: border-box;
}

body {
  background-color: var(--background-color);
  overflow-y: scroll;
}

.history__image {
  opacity: 0;
  border: 2px solid #ff0;
  border-radius: 20px;
  width: 300px;
  margin-left: auto;
  margin-right: auto;
  animation: 1.2s forwards fadeInZoom;
  display: block;
  transform: scale(.9);

  @media screen and (width >= 1200px) {
    width: 500px;
  }
}

@keyframes fadeInZoom {
  to {
    opacity: 1;
    transform: scale(1);
  }
}

.history__description {
  opacity: 0;
  transition: all .8s;
  transform: translateY(30px);
}

.history__description.visible {
  opacity: 1;
  transform: translateY(0);
}

.history__description, .history__vovk-before, .history__description-vovk, .history__vovk-after {
  opacity: 0;
  transition: opacity .8s, transform .8s;
  transform: translateY(30px);
}

.history__vovk-after {
  border: 2px solid #ff0;
  border-radius: 20px;
  width: 300px;
  margin-left: auto;
  margin-right: auto;
  display: block;

  @media screen and (width >= 1200px) {
    width: 500px;
  }
}

.history__vovk-before {
  border: 2px solid #ff0;
  border-radius: 20px;
  width: 300px;
  margin-left: auto;
  margin-right: auto;
  display: block;

  @media screen and (width >= 1200px) {
    width: 500px;
  }
}

.history__vovk-before {
  transform: translateX(-40px);
}

.history__vovk-after {
  transform: translateX(40px);
}

.visible {
  opacity: 1;
  transform: translate(0);
}

.debug {
  outline: 1px dashed #00000014;
  padding: 10px;
}

.history__title {
  text-align: center;
  margin-bottom: 100px;
}
